.login_logo{
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}
.login{
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.login_container{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding:20px ;
}
.sign_h1{
    font-weight: 500;
    margin-bottom: 20px;
}
.pass_h5{
    margin-bottom: 5px;
}
.input_box{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}
p{
    margin-top: 15px;
    font-size: 12px;
}
.sign_in_btn{
background: #f0c14b;
border-radius: 2px;
width: 100%;
height: 30px;
border: 1px solid;
margin-top: 10px;
border-color: #a88734 #9c7e31 #846a29;
}
.register_btn{
border-radius: 2px;
width: 100%;
height: 30px;
border: 1px solid;
margin-top: 10px;
border-color: darkgray;
}
